import React, { Component, lazy } from "react";
import { Link } from "react-router-dom";
import { formatDate, localStoragedecryp, localStorageencryp } from "../../../../components/helpers/function";
import { ToastNotification } from "../../../../components/helpers/notification";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../../components/panel/panel";
import { getData } from "../../../../config/axios";
import * as moment from "moment-timezone";
import { getDataSales } from "../../../../actions/datamaster_action";
import { connect } from "react-redux";
const HeadFormLaporanPembelianBarangHistory = lazy(() =>
  import("./HeadFormLaporanPembelianBarangHistory")
);


class FormLaporanPembelianBarangHistory extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanTransaksiPembelian: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
    this.props.dispatch(getDataSales())
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
      let data_post = {
        tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
        tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
        kode_group: data.kode_group,
        jenis_group: data.jenis_group,
        kondisi: data.kondisi,
      };
      getData(`pembelian/report-selisih?tanggal[gte]=${data_post.tgl_awal}&tanggal[lte]=${data_post.tgl_akhir}&kode_group=${data_post.kode_group}&jenis_group=${data_post.jenis_group}&kondisi${data_post.kondisi}`)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            isLoading: false,
            export: false,
          });
        } else {
          ToastNotification("success", "Laporan Transaksi Pembelian Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            LaporanTransaksiPembelian: [],
          });
          this.setState({
            export: true,
            isLoading: false,
            LaporanTransaksiPembelian: res.data,
          });
        }
      })
      .catch((error) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          isLoading: false,
          export: false,
        });
      });
    
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">
            Laporan History Transaksi Pembelian
          </li>
        </ol>
        <h1 className="page-header">Laporan History Transaksi Pembelian </h1>
        <Panel>
          <PanelHeader>Laporan History Transaksi Pembelian</PanelHeader>
          <PanelBody>
            {/* {console.log(this.state.hidden)} */}
            <HeadFormLaporanPembelianBarangHistory
              export={this.state.export}
              isLoading={this.state.isLoading}
              dataSales={this.props.dataSales}
              LaporanTransaksiPembelian={this.state.LaporanTransaksiPembelian}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}
export default connect((state) => {
  return {
    dataSales : state.datamaster.getDataSales
  }
})(FormLaporanPembelianBarangHistory);
